@media (max-width: 480px) {
  .wSummary, .wSkills .wEducation, .wExperience{
    outline: 0.2rem solid;
    background-color: #FF9800;
  }
}

.pSummaryHead, .pSkillsHead {
  margin-top: 0;
}

.pSummaryContent {
  margin-top: 0;
}

.listContent{
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 2%;
  padding-bottom: 2%;
}

.skills-list-content{
  display: grid;
  grid-template-columns: 1fr 6rem 1rem 6rem 2fr;
}

.skill-star{
  grid-column: 4/5;
}

.skill-text{
  grid-column: 2/3;
  text-align: right;
}


